<script>
	import https from '../api/https'
	export default {
		name: 'VIP',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                fixNumber: https.fixNumber,
                showModelBox: false,
                selectYearId: 1,
                VipList: [],
                myLevel: '',
                selectVipItem: null,
                expire: '',
                upgreadSuccessText: '',
                upgradeSuccessBox: false,
                payUrl: '',
                pamentBox: false,
			}
		},
		created(){
			this.getData()
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		methods: {
			getData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/user/rule/vip').then((data) => {
					this.$toast.clear()
                    this.showLoading = false
					let res = data.data
					if(res.code == 0) {
						this.VipList = res.data.levels
                        this.myLevel = res.data.myLevel.id
                        if(res.data.myLevel.expire) {
                            this.expire = res.data.myLevel.expire
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            payUpgread(item,myLevel){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Preparing...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'upgrade',
                    product: item.id+":"+this.selectYearId
                }
				https.fetchPost('/api/pay/cashier/create',params).then((data) => {
					this.$toast.clear()
                    this.showLoading = false
					let res = data.data
					if(res.code == 0) {
                        if(res.data.upgrade && res.data.upgrade.id) {//balance pay success
                            if(item.id == myLevel) {
                                this.upgreadSuccessText = 'You have successfully renewed for '+res.data.upgrade.year+' years'
                                this.upgradeSuccessBox = true
                            }else {
                                this.upgreadSuccessText = 'YYou have successfully upgraded to '+res.data.upgrade.name+'.'
                                this.upgradeSuccessBox = true
                            }
                            this.showModelBox = false
                            this.getData()
                        }else if(res.data.openUrl) {
                            this.payUrl = res.data.openUrl
                            this.pamentBox = true
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
                        this.showLoading = false
						this.$toast({
							message: 'Unable to\ninitiate payment',
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
				})
            },
            goUpgrade(item){
                this.selectVipItem = item
                this.showModelBox = true
            },
            selectYear(index){
                this.selectYearId = index
            },
            clickContinue(){
				this.pamentBox = false
                this.showModelBox = false
                this.getData()
			},
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
